export default {
	data() {
		return {
			data:[],
			pageIndex:0
		};
	},
	onLoad:function(){
		
	},
	onShow:function(){
		this.getOrderList();
	},
	onReachBottom:function(){
		this.pageIndex=this.pageIndex+1;
		this.getOrderList();
	},
	methods:{
		getOrderList:function(){
			uni.showLoading({title:'加载中'})
			uni.request({
				url:this.$url+'getRefundsList',
				data:{pageIndex:this.pageIndex},
				success:(res)=>{
					console.log(res)
					for(var i in res.data){
						res.data[i].totalPrice=parseFloat(res.data[i].totalPrice)+parseFloat(res.data[i].expressCost)
					}
					if(this.pageIndex==0)
						this.data=res.data;
					else
						this.data=this.data.concat(res.data);
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		navigateToGoods:function(data){
			uni.navigateTo({
				url: '../refunds-details/refunds-details?orderInfo='+JSON.stringify(data)
			})
		},
	}
}
